	position:relative;
	z-index:9;
	background:#ccc;
	height:46px;
	text-align:center;
	line-height:46px;
}
.send-btn{
	width:40%;
	background:#516df5;
	display:inline-block;
	line-height:30px;
	height:30px;
	border-radius:8px;
	color:white;
	font-style:normal;
	font-size:14px;
}
.update-customer-content{
    /*height:300px;*/
    position: relative;
	padding:0 10px;
